.work2-container {
    padding-top: 150px;
}

//containers
.work-flex-container {
    width: 100%;
}

.project-col {
    width: 50%;
    display: inline-block;
    position: relative;
}

.project-container {
    width: 85%;
    height: 45vh;
    margin: auto;
    margin-bottom: 1.5rem;
    position: relative;

    &:hover{ 

        .info {
            display: inline;
            background-color: #003366;
            transform: translateY(0);
        }

        .image2 {
            filter: blur(3px);
        }

    }
}


//title
.title-con {
    position: absolute;
    width: 50%;
    margin: 0;
    height: 100%;
    display: flex;
    align-items: center;

    .project-title {
        text-decoration: none;
        margin: auto;
        text-align: center;
        z-index: 200;
        font-weight: 800;
        font-size: 2rem;
        color: #003366;
        transition: all 300ms ease-out;
    }
}
.project-title:hover {
    border-bottom: 2px solid #ff3300;
}

//picture
.side-con {
    position: absolute;
    display: inline-block;
    margin-left: 25%;
    width: 75%;
    height: 100%;
}

.image-con {
    position: absolute;
    height: 100%;
    display: flex;
    align-items: center;
}

.image2 {
    width: 100%;
    background-color: rgba(0,0,0,1);
    filter: blur(1px);
    transition: all 300ms ease-in;
}


//information 
.info-con {
    position: absolute;
    text-align: center;
    bottom: 0;
    width: 100%;
    overflow: hidden;

}

.info {
     width: 90%;
     margin:auto;
     transition: all 300ms ease-in;
     transform: translateY(-120%);
     font-size: 1.5rem;
     color: #ff3300;
}


 //Mobile
 @media only screen and (max-width: 641px) {

    .project-col {
        width: 100%;
    }
    
 }