@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

$body-bg-color: white;

body {
    font-family: 'Montserrat', sans-serif;
    background-color: $body-bg-color;
}

.logo-container {
    position: absolute;
    overflow: hidden;
    width: 100%;
}
.cls-1{
    fill:none;
    opacity: .2;
    stroke:#F23005;
    stroke-width:10px;
}


@media only screen and (max-width: 600px) {
    .cls-1{
        opacity: .1;
    }
}